@charset "UTF-8";

#mainVisual {
    padding:0 0 194px;
    .visual_wrap {height:730px; position:relative;}
    .visual_img {
        width:100%; height:100%; position:relative; overflow:hidden;
        li {
            width:0; height:730px; position:absolute; top:0; left:0;
            @include backgroundSoltC;
            &:first-child {width:100%;}
        }
    }
    .visual_box {
        width:780px; height:333px; padding:80px 0 0 50px; background-color:$blue; position:absolute; bottom:-194px; left:50%; margin:0 0 0 -190px; z-index:15;
        @include boxSizing;
        .visual_txt {
            height:100%; position:relative; color:$white;
            li {
                width:100%; position:absolute; top:0; left:0;
                &:first-child {
                    display:block; @include opacity(1);
                    p.tit,
                    p.txt,
                    a {display:block; @include opacity(1)}
                }
            }
            p.tit {
                font-size:50px; line-height:55px; display:none;
                @include opacity(0);
                @include hk;
            }
            p.txt {
                font-size:18px; line-height:30px; font-weight:$light; padding:30px 0 0; display:none;
                @include opacity(0);
            }
            a {
                width:50px; height:50px; text-indent:-9999px; position:absolute; bottom:5px; right:44px; display:none;
                @include opacity(0);
                @include boxSizing;
                @include borderSet(1px, solid, $white);
                @include transition(background-color, .2s);
                &:before,
                &:after {
                    width:10px; height:2px; display:block; content:""; background-color:$white; position:absolute; top:48%; left:19px;
                    @include transition(background-color, .2s);
                }
                &:before {@include rotate(45); margin:-3px 0 0;}
                &:after {@include rotate(-45); margin:3px 0 0;}
                &:hover,
                &:focus {
                    background-color:$white;
                    &:before,
                    &:after { background-color:$blue;}
                }
            }
        }
        .indi_wrap {
            position:absolute; top:37px; right:44px;
            @extend %clearfix;
            #mainIndi {
                float:left; height:13px; position:relative; top:-5px;
                li {width:11px; display:inline-block; margin:0 10px;}
                button {
                    vertical-align:middle;
                    &:hover span,
                    &:focus span,
                    &.active span {@include opacity(1);}
                    span {
                        width:11px; height:11px; text-indent:-9999px; background-color:$white; display:block;
                        @include opacity(.3);
                        @include borderRadius(100%);
                        @include transition(opacity, .2s);
                    }
                }
            }
            #visualController {
                width:9px; height:13px; display:inline-block; text-indent:-9999px; margin:0 0 0 10px; float:right;
                @include backgroundSolt;
                @include transition(background-image, .2s);
                &.stop { background-image:url(../../images/icon/stop_icon.gif);}
                &.play { background-image:url(../../images/icon/play_icon.gif);}
            }
        }
    }
}

//메인
#mainContainer {
    padding:140px 0 0; overflow:hidden;
    section {
        margin:110px 0 0;
        .sec_tit {
            width:1180px; margin:0 auto;
            h3 {
                font-size:45px; font-weight:$bold; color:$black; position:relative;
                &:before {
                    width:36px; height:10px; display:block; content:""; background-color:$blue; position:absolute; top:18px; left:-63px;
                    @include skew(-40, 0);
                }
            }
            p.txt {padding:15px 0 0; line-height:25px; color:$gray2;}
        }
    }
    
    //기업정보
    #mainCompany {
        .con_box {
            width:100%; height:596px; position:relative; background-image:url(../../images/main/main_bg01.jpg); margin:40px 0 0;
            @include backgroundSolt(50%, 0);
            @include transition(background-image, .2s);
            &.bg00 {background-image:url(../../images/main/main_bg02.jpg);}
            &.bg01 {background-image:url(../../images/main/main_bg03.jpg);}
            &.bg02 {background-image:url(../../images/main/main_bg04.jpg);}
            .txt_wrap {
                width:1180px; height:100%; margin:0 auto; display:table; position:relative;
                p.txt {
                    display:table-cell; vertical-align:middle; position:relative; font-size:40px; color:$white;
                    @include hk;
                }
                ol {
                    position:absolute; z-index:5; top:276px; right:0;
                    font-weight:$medium;
                    li {
                        margin:7px 0 0;
                        &:first-child {margin:0;}
                    }
                    a {
                        width:240px; height:55px; display:block; background-color:$white; padding:0 0 0 20px; font-size:16px; line-height:55px; position:relative;
                        @include boxSizing;
                        @include transition(color, .2s);
                        
                        &:before,
                        &:after {
                            width:10px; height:2px; display:block; content:""; background-color:$gray2; position:absolute; top:48%; right:22px;
                            @include transition(background-color, .2s);
                        }
                        &:before {@include rotate(45); margin:-3px 0 0;}
                        &:after {@include rotate(-45); margin:3px 0 0;}
                        
                        &:hover,
                        &:focus {
                            color:$blue;
                            &:before,
                            &:after {background-color:$blue;}
                        }
                    }
                }
            }
        }
    }
    
    //주요제품
    #productSlide {
        height:730px; position:relative; margin:40px 0 0;
        .txt_slide {
            width:580px; height:350px; position:absolute; z-index:5; background-color:$gray5; left:110px; top:107px;
            @include boxShadow(70px 65px 130px -90px #000);
            article {
                width:100%; height:100%; position:absolute; display:none; top:0; left:0; padding:65px 50px 0 60px;
                @include boxSizing;
                @include opacity(0);
                &:first-child {display:block; @include opacity(1);}
                h1 {
                    font-size:24px; color:$blue;
                    @include hk;
                }
                p.name {font-size:46px; font-weight:$bold; color:$blue; padding:12px 0 0;}
                p.info {width:400px; line-height:32px; font-size:18px; color:$gray2; padding:25px 0 0; font-weight:$medium;}
                a {
                    width:153px; height:62px; display:block; background-color:$black; line-height:62px; color:$white; font-size:16px; text-indent:33px;
                    position:absolute; left:185px; bottom:-31px;
                    @include transition(background-color,.2s);
                    &:after {
                        width:13px; height:13px; display:block; content:""; background:url(../../images/icon/more_icon.png) 50% 50% no-repeat;
                        position:absolute; top:50%; margin:-6px 0 0; left:113px;
                        @include transition(transform, .2s);
                    }
                    &:hover,
                    &:focus {
                        background-color:$blue;
                        &:after {@include rotate(180);}
                    }
                }
            }
        }
        .img_slide {
            width:100%; position:relative; margin:0 auto;
            &:after {
                width:100px; height:588px; display:block; content:""; position:absolute; right:0; background-color:$white;
                @include transition(transform,.2s);
                @media screen and (max-width: 1400px) {
                    @include translate(50px,0);
                }
            }
            //&:before {width:200px; height:588px; display:block; content:""; position:absolute; left:50%; margin:0 0 0 -790px; background-color:$white; z-index:1;}
            .img_wrap {
                width:100%; height:588px; position:absolute; right:0;
                div {
                    width:92%; height:588px; position:absolute; left:50%;
                    @include translate(-590px,0);
                    @include backgroundSoltC(50%, 50%);
                    @include opacity(0);
                    &:first-child {@include opacity(1);}
                }
            }
            .slide_nav {
                display:block; content:""; position:absolute; top:220px; right:100px; z-index:10;
                @include transition(transform,.2s);
                @media screen and (max-width: 1400px) {
                    @include translate(50px,0);
                }
                button {
                    width:72px; height:72px; display:block; text-indent:-9999px; position:relative; background-color:$white; margin:0 0 2px;
                    @include transition(background-color, .2s);
                    &:before,
                    &:after {
                        width:10px; height:2px; display:block; content:""; background-color:$blue; position:absolute; top:48%; right:30px;
                        @include transition(background-color, .2s);
                    }
                    &:before {@include rotate(45); margin:-3px 0 0;}
                    &:after {@include rotate(-45); margin:3px 0 0;}
    
                    &:hover,
                    &:focus {
                        background-color:$blue;
                        &:before,
                        &:after {background-color:$white;}
                    }
                    &#prevBtn {@include rotate(180);}
                }
            }
        }
        .category_slide {
            width:1180px; height:126px; margin:0 auto; position:relative; top:605px; background-image:url(../../images/main/product_list_on.jpg);
            @include backgroundSolt(50%, 100%);
            .category_left,
            .category_right {height:100%; position:absolute; z-index:10; background-image:url(../../images/main/product_list.jpg); background-color:$white;}
            .category_left {
                width:0; left:0;
                @include backgroundSolt(0, 100%);
            }
            .category_right {
                width:90%; right:0;
                @include backgroundSolt(100%, 100%);
            }
            ul {
                width:100%; height:100%; position:absolute; z-index:20;
                @extend %clearfix;
                li {width:10%; height:100%; float:left;}
                button {
                    width:100%; height:100%; border-bottom-style:solid; border-bottom-width:3px; border-bottom-color:transparent; text-indent:-9999px;
                    @include transition(border-color,.2s);
                    &:hover,
                    &:focus {border-bottom-color:$blue;}
                }
            }
            .active_dot {width:0; height:0; display:block; border-left:10px solid transparent; border-right:10px solid transparent; border-top:11px solid $white; position:absolute; top:0; left:50px;}
        }
    }
    
    //지속가능경영
    #mainOperation {
        ol {
            width:1180px; margin:30px auto 0;
            @extend %clearfix;
            li {
                width:228px; float:left; margin:0 0 0 10px; position:relative;
                &:first-child {margin:0;}
                a {
                    display:block;
                    &:hover,
                    &:focus {
                        img {@include scale(1.1);}
                        p {
                            color:$blue;
                            &:after {@include translate(5px,0);}
                        }
                    }
                }
                .img_wrap {
                    width:100%; height:228px; overflow:hidden;
                    img {
                        width:100%;
                        @include transition(transform,.2s);
                    }
                }
                p {
                    font-weight:$medium; margin:19px 0 0; font-size:19px; display:inline-block; position:relative;
                    &:after {
                        width:23px; height:7px; display:block; content:""; background:url(../../images/icon/right_arrow.gif) 50% 50% no-repeat; position:absolute;
                        right:-40px; top:50%; margin:-4px 0 0;
                        @include transition(transform,.2s);
                    }
                }
            }
        }
    }
    
    //도레이 소식
    #mainNews {
        background-color:$gray5; padding:100px 0;
        .list_wrap {
            width:1180px; margin:55px auto 0; position:relative;
            a {
                display:block;
                &:hover,
                &:focus {
                    img {@include scale(1.05);}
                    h1 {color:$blue; text-decoration:underline;}
                }
            }
            .img_wrap {
                width:100%; height:310px; overflow:hidden;
                img {
                    width:100%;
                    @include transition(transform,.2s);
                }
            }
            .txt_wrap {
                width:500px; padding:30px 0 0 23px;
                p {
                    font-size:18px; color:$blue;
                    @include hk;
                }
                h1 {font-weight:$medium; font-size:23px; line-height:35px; padding:10px 0 0;}
            }
        }
        .owl-nav {
            font-size:46px; position:absolute; right:0; top:-95px;
            button {
                margin:0 0 0 10px;
                &:hover,
                &:focus {
                    color:$white;
                    span {background-color:$blue;}
                }
            }
            span {
                width:36px; height:36px; display:block; line-height:24px; background-color:transparent;
                @include transition;
            }
        }
    }
    
    //채용정보
    #mainCareers {
        height:380px; position:relative; margin:0;
        a {position:absolute; left:50%; @include translate(-50%, 0);}
    }
}









































