@charset "UTF-8";

/* Font style setting */
$noto: Noto Sans KR, sans-serif;
$hk: HKNova-Medium, sans-serif;

/* Point color setting */
$blue : #004098;
$black : #000;
$white : #fff;

$gray1 : #333;
$gray2 : #636363;
$gray3 : #a8a8a8;
$gray4 : #d6d6d6;

$gray5 : #f1f1f1;


/* Background - color setting */
$bg-blue : $blue;
$bg-black : $black;
$bg-grey1 : $gray1;
$bg-grey2 : $gray2;
$bg-grey3 : $gray3;

/* Border - color setting */
$bd-blue : $blue;
$bd-black : $black;
$bd-grey1 : $gray1;
$bd-grey2 : $gray2;
$bd-grey3 : $gray3;

/* Font - color setting */
$txt-blue : $blue;
$txt-black : $black;
$txt-grey1 : $gray1;
$txt-grey2 : $gray2;
$txt-grey3 : $gray3;

/* Input - color setting */
$inp-bd-base : $gray1;

/* Button - color setting */
$btn-black : $black;

/**
 * BASE SETTING
 */
$base-font : $noto;
$base-font-size : 17px;
$base-font-color : $txt-grey1;

$light : 300;
$medium : 500;
$bold : 700;

