@charset "UTF-8";

html, body { max-width:100%;}
html.no_scroll { overflow:hidden; padding:0 17px 0 0;}
body { min-width:1280px; margin:0 auto;}
.dimmed {
    width:100%; height:100%; position:fixed; z-index:80; background-color:#000; display:none; top:0;
    @include opacity(0);
}

//스킵 네이게이션
#skipNavigation a {
    position:absolute; top:-100px; left:0; z-index:100; width:100%;border-bottom:1px solid #dbdbdb; background:#f8f7f3; line-height:50px;
    &:focus {top:0; text-align:center;}
}

//헤더
#header {
    width:100%; height:140px; position:absolute; z-index:100; background-color:$white; overflow:hidden;
    &:after {width:100%; height:1px; display:block; content:""; background-color:#c3c3c3; position:absolute; top:141px; left:0;}
    .header_wrap {width:1180px; margin:0 auto;}
    .header_top {
        position:relative;
        @extend %clearfix;
        #logo {
            float:left; margin:22px 0 0;
            a {width:303px; height:54px; display:block; background:url(../../images/layout/logo.jpg) 50% 50% no-repeat; text-indent:-9999px;}
        }
        .header_util {
            position:absolute; top:40px; right:0;
            @extend %clearfix;
            .language_link {
                font-size:13px; height:26px; line-height:26px;
                @extend %clearfix;
                ol {
                    float:left; margin:0 20px 0 0;
                    @include hk;
                    li {
                        float:left; padding:0 0 0 12px; position:relative;
                        &:before {width:2px; height:2px; display:block; content:""; background-color:$gray3; top:50%; margin:-2px 0 0; left:6px; position:absolute;}
                        &:first-child:before {display:none;}
                    }
                    a {
                        color:$gray3;
                        @include transition(color, .2s);
                        &:hover,
                        &.active {color:$gray2;}
                    }
                }
            }
            .contact_link {
                width:75px; float:left; display:block; background-color:$gray2; color:$white; text-align:center; font-weight:$medium;
                @include borderRadius(3px);
                @include transition;
                &:hover,
                &:focus {background-color:$blue;}
            }
        }
    }
    
    //gnb
    #gnb {
        margin:10px 0 0; font-size:16px; position:relative;
        .depth1 {
            @extend %clearfix;
            > li {
                float:left; margin:0 105px 0 0; position:relative;
                &:hover > a,
                &:focus > a {color:$blue;}
                > a {
                    font-weight:$medium; color:$black; line-height:53px;
                    @include transition(color,.2s);
                    &.active {color:$blue;}
                }
            }
        }
        .depth2 {
            width:145px; position:absolute; padding:30px 0;
            li {
                padding:15px 0 0;
                &:first-child {padding:0;}
                a {
                    display:inline-block; position:relative;
                    &:hover,
                    &:focus,
                    &.active {color:$blue;}
                    &.active:after {
                        width:100%; height:1px; display:block; content:""; position:absolute; bottom:-4px; left:0; background-color:$blue;
                    }
                }
            }
        }
        .gnb_link {
            position:absolute; top:84px; right:0;
            li {
                margin:9px 0 0;
                &:first-child {margin:0;}
            }
        }
    }
}

//푸터
#footer {
    .footer_wrap {
        width:1180px; margin:0 auto; position:relative;
        .f_gnb {
            border-bottom:1px solid $gray4;
            ul {
                font-size:15px; padding:25px 0 23px; display:inline-block;
                @extend %clearfix;
            }
            li {
                float:left; padding:0 0 0 20px; position:relative;
                &:before {width:1px; height:13px; display:block; content:""; background-color:$gray2; position:absolute; left:10px; top:1px;}
                &:first-child {
                    padding:0;
                    &:before {display:none;}
                    a {color:$blue; font-weight:$medium;}
                }
                a {
                    color:$gray2;
                    @include transition(color,.2s);
                    &:hover,
                    &:focus {color:$blue;}
                }
            }
        }
        .f_box {
            padding:40px 0 35px; text-align:center;
            .company_info {
                font-size:15px; color:$gray2; font-weight:$medium; line-height:25px; margin:35px 0 0;
                address {display:inline-block;}
                p.tel {display:inline-block;}
            }
        }
    }
    
    #familySite {
        width:220px; height:41px; border-style:solid; border-color:$gray4; border-width:1px; position:absolute; right:0; bottom:205px; font-weight:$medium;
        background-color:$white; overflow:hidden; z-index:11; font-size:15px;
        @include boxSizing;
        @include transition(background-color,.2s);
        &.on {
            background-color:$gray5;
            button {
                &:after { border-top-color:$gray1; border-bottom-color:transparent; top:19px;}
            }
        }
        button {
            width:100%; height:42px; line-height:42px; padding:0 13px; text-align:left; color:$gray1; position:absolute; bottom:0; font-size:15px;
            @include transition(color,.2s);
            &:after {
                width:0; height:0; display:block; content:""; position:absolute; right:13px; top:9px;
                border-right: 5px solid transparent; border-left: 5px solid transparent;
                border-top-color:transparent; border-top-style:solid; border-top-width:8px;
                border-bottom-color:$gray1; border-bottom-style:solid; border-bottom-width:8px;
            }
        }
        div {
            width:100%; position:absolute; right:-1px; bottom:41px; padding:0 13px;
            @include boxSizing;
            ul {
                line-height:34px; border-bottom:1px solid $gray3; padding:9px 0;
                @extend %clearfix
            }
            li {
                width:100%;
                a {
                    @include transition(color,.2s);
                    &:hover,
                    &:focus { color:$blue;}
                }
            }
        }
    }
}