@charset "UTF-8";

//기타 정책
.etc {
    .etc_wrap {width:1180px; margin:0 auto;}
    .txt_wrap {
        height:475px; padding:70px 90px; border:2px solid $gray2; font-size:16px; line-height:30px; overflow-y:scroll;
        white-space:pre-line;
        @include boxSizing;
    }
    //쿠키정책
    .cookie_etc {
        strong {font-weight:$medium;}
        a {color:$blue;}
    }
    .cookie_info {
        text-align:center; padding:65px 0 0;
        strong {font-size:17px; color:$blue;}
        p {padding:15px 0 0; font-size:15px;}
        a {
            width:200px; height:55px; line-height:51px; background-color:$blue; color:$white; display:inline-block; cursor:pointer; font-size:20px; margin:40px 0 0; font-weight:$medium;
            @include borderSet(2px, solid, $blue);
            @include transition;
            @include boxSizing;
            &:hover {background-color:$white; color:$blue;}
        }
    }
}

//고객문의
.inquiry {
    .inquiry_wrap {width:1180px; margin:0 auto;}
    h1 {font-size:35px; line-height:50px; font-weight:$medium; color:$black;}
    .write_wrap {
        margin:40px 0 0;
        em {color:#ff1212; vertical-align:middle;}
        > p {text-align:right; font-weight:$bold; margin:0 0 10px;}
    }
    table {
        border-top:2px solid $gray2; border-bottom:2px solid $gray2;
        tr {
            border-top:1px solid $gray4;
            &:first-child {border-top:0;}
        }
        th {font-size:16px; font-weight:$medium; color:$black;}
        td {
            min-height:46px; padding:15px 0;
            select,
            textarea,
            input[type=text] {
                height:46px; background-color:#f6f6f6; font-weight:$light;
                @include borderSet(1px, solid, $gray4);
                @include boxSizing;
            }
            .select_box {
                position:relative;
                &:after {
                    width:0; height:0; border-top:8px solid $black; border-left:6px solid transparent; border-right:6px solid transparent; display:block; content:"";
                    position:absolute; top:19px; right:15px; z-index:1;
                }
            }
            input[type=text] {padding:0 0 0 15px;}
            .inquiry_category {
                line-height:46px;
                @extend %clearfix;
                .input_box {
                    float:left; position:relative; padding:0 45px 0 35px;
                    input[type=radio] {
                        position:absolute; left:10px; top:15px;
                        &:checked {
                            & ~ label:after {@include opacity(1);}
                        }
                    }
                    label {
                        color:$gray1; font-size:16px;
                        &:before {
                            width:26px; height:26px; display:block; content:""; background-color:#f6f6f6; -webkit-appearance:none; position:absolute; cursor:pointer; vertical-align:sub;
                            top:10px; left:0;
                            @include borderSet(1px, solid, $gray4);
                            @include boxSizing;
                            @include borderRadius(100%);
                        }
                        &:after {
                            width:12px; height:12px; display:block; content:""; position:absolute; top:17px; left:7px; background-color:$blue !important; z-index:1;
                            @include borderRadius(100%);
                            @include opacity(0);
                            @include transition(opacity, .2s);
                        }
                    }
                }
            }
            .select_wrap {
                @extend %clearfix;
                .select_box {
                    float:left; margin:0 0 0 15px;
                    @include boxSizing;
                    &:first-child {margin:0;}
                    &:nth-of-type(4) select {width:294px;}
                    select {width:220px; height:46px; padding:0 0 0 15px;}
                    &:after {
                        width:0; height:0; border-top:8px solid $black; border-left:6px solid transparent; border-right:6px solid transparent; display:block; content:"";
                        position:absolute; top:19px; right:15px; z-index:1;
                    }
                }
            }
            //이름 인풋
            .name_input {width:455px;}
            //메일 인풋
            .mail_input {
                position:relative;
                @extend %clearfix;
                &:after {width:33px; height:46px; display:block; content:"@"; position:absolute; left:220px; top:15px; text-align:center;}
                .select_box,
                input[type=text] {float:left;}
                select {width:202px; padding:0 0 0 15px;}
                .mail_input01 {width:220px; margin:0 33px 0 0;}
                .mail_input02 {width:220px; margin:0 0 0 15px;}
                p {display:inline-block; font-size:15px; color:$gray2; line-height:46px; margin:0 0 0 15px;}
            }
            //휴대폰 인풋
            .tel_input {
                .select_box,
                input[type=text] {width:130px; display:inline-block;}
                input[type=text] {padding:0 0 0 15px;}
                select {width:100%; padding:0 0 0 15px;}
                span {display:inline-block; margin:0 10px;}
            }
            .subject_input {width:100%;}
            textarea {width:100%; height:180px; resize:none; padding:10px 15px;}
        }
    }
    .agree_wrap {
        padding:90px 0 0;
        h2 {font-size:24px; color:$black;}
        .txt_wrap {
            height:320px; padding:40px; border:1px solid $gray4; margin:35px 0 0;
            @include boxSizing;
            p {
                height:100%; overflow-y:scroll; line-height:26px; color:$gray2; padding:0 10px 0 0;
                
                @include boxSizing;
                strong {
                    font-weight:$medium; margin:20px 0 0; display:block;
                    &:first-child {margin:0;}
                }
                span {margin:10px 0 0; display:block;}
            }
        }
        .agree_check {
            margin:20px 0 0; line-height:24px;
            @extend %clearfix;
            p {font-size:18px; font-weight:$medium; color:$black; float:left; padding:0 25px 0 0;}
            .check_box {
                float:left;
                .input_box {
                    float:left; position:relative; padding:0 20px 0 30px;
                    input[type=radio] {
                        position:absolute; left:5px; top:7px;
                        &:checked {
                            & ~ label:after {@include opacity(1);}
                        }
                    }
                    label {
                        color:$gray1; font-size:16px;
                        &:before {
                            width:24px; height:24px; display:block; content:""; background-color:#f6f6f6; -webkit-appearance:none; position:absolute; cursor:pointer; vertical-align:sub;
                            top:0; left:0;
                            @include borderSet(1px, solid, $gray4);
                            @include boxSizing;
                        }
                        &:after {
                            width:12px; height:12px; display:block; content:""; position:absolute; top:6px; left:6px; background-color:$blue !important; z-index:1;
                            @include opacity(0);
                            @include transition(opacity, .2s);
                        }
                    }
                }
            }
        }
        .agree_submit {
            text-align:center; padding:70px 0 0;
            .submit_btn {
                width:200px; height:55px; line-height:51px; background-color:$blue; color:$white; display:inline-block; cursor:pointer; font-size:20px;
                @include borderSet(2px, solid, $blue);
                @include transition;
                @include boxSizing;
                &:hover {background-color:$white; color:$blue;}
            }
        }
    }
}

//검색
.search {
    .search-wrap {
        max-width:1180px; margin:0 auto; overflow:hidden;
        
        /* 상단 탭메뉴 */
        .search-tab-list {
            margin:0 -7px;
            ul {
                @extend %clearfix;
                border-bottom:2px solid #183F97;
                li {
                    float:left; margin:0 7px;
                    button {
                        width:384px; height:62px; font-size:17px; line-height:62px; letter-spacing:-0.25px; color:#000; font-weight:500;
                        background-color:#F6F6F6;
                    }
                    &.active {
                        button {background-color:#183F97; color:#fff;}
                    }
                }
            }
        }
        
        /* 검색 폼 부분 */
        .search-form {
            @extend %clearfix;
            padding:40px 22px; border-bottom:1px solid #183F97;
            .search-form-left {
                float:left;
                .search-select {
                    @extend %clearfix;
                    select {
                        float:left; width:223px; height:46px; padding:0 30px 0 15px; border:1px solid #D6D6D6; background:#F6F6F6 url(../../images/icon/select_arrow.png) no-repeat 190px center; margin-left:15px; font-size:16px; letter-spacing:-0.25px; color:#636363; font-weight:300;
                        &:first-child {margin-left:0;}
                    }
                }
                input {width:905px; padding:0 15px; border:1px solid #D6D6D6; background:#F6F6F6; height:45px; font-size:16px; letter-spacing:-0.25px; color:#636363; font-weight:300; margin-top:17px;}
            }
            .search-form-right {
                float:right;
                button {width:183px; height:110px; font-size:20px; line-height:110px; letter-spacing:-0.25px; color:#fff; font-weight:500; background-color:#333; cursor:pointer;
                    img {display:inline-block; vertical-align:middle; margin-right:10px;}
                }
            }
        }
        
        /* 검색결과가 없을 때 */
        .search-no-data {
            padding:150px 0;
            .no-data-icon {width:130px; height:130px; line-height:130px; border-radius:50%; background-color:#183F97; text-align:center; margin:0 auto;
                img {display:inline-block; vertical-align:middle;}
            }
            p {text-align:center; margin-top:25px; font-size:20px; letter-spacing:-0.25px; color:#004098; font-weight:500;}
        }
    }
    
    /* 검색결과 리스트 */
    .search-list {
        .search-list-top {padding:95px 0 30px 0; text-align:center;
            .search-word p {
                font-size:27px; line-height:50px; letter-spacing:-0.25px; color:#000; font-weight:500; margin:0 0 20px 0;
                span {color:#a8a8a8; margin:0 10px;}
            }
            .search-result p {font-size:16px; color:#636363; font-weight:500;
                span {color:#f00;}
            }
        }
        
        .search-list-inner {padding:40px 50px; border:2px solid #A8A8A8; border-radius:10px; margin:20px 0;
            .search-tit {
                position:relative;
                padding-bottom:20px; border-bottom:1px solid #D6D6D6; margin-bottom:30px;
                h3 {position:relative; max-width:710px; word-break:break-all; font-size:30px; letter-spacing:-0.25px; color:#004098; font-weight:600; padding-left:40px;
                    &::before {content:""; position:absolute; left:0; top:50%; margin-top:-5px; width:30px; height:10px; background-color:#004098; display:block; -webkit-transform:skew(-40deg, 0deg); -moz-transform:skew(-40deg, 0deg); -ms-transform:skew(-40deg, 0deg); -o-transform:skew(-40deg, 0deg); transform:skew(-40deg, 0deg); -webkit-transition:width 1s ease; -moz-transition:width 1s ease; -o-transition:width 1s ease; -ms-transition:width 1s ease;}
                }
                .location {position:absolute; right:0; bottom:20px; font-size:14px; letter-spacing:-0.25px; color:#636363; font-weight:500;
                    span {
                        &::before {content:""; display:inline-block; margin:0 7px; width:5px; height:9px; background:url(../../images/icon/location_arrow.png) no-repeat center;}
                        &:first-child {
                            padding-left:0;
                            &::before {display:none;}
                        }
                    }
                }
            }
            
            .search-info {
                @extend %clearfix;
                .search-info-left {float:left;
                    ul {
                        @extend %clearfix;
                        li {float:left; width:425px;
                            h4 {font-size:19px; letter-spacing:-0.25px; color:#000; font-weight:500; margin-bottom:20px;}
                            p {font-size:16px; line-height:28px; letter-spacing:-0.25px; color:#636363; font-weight:300;}
                        }
                    }
                }
                
                .search-info-right {float:right;
                    button {display:block; width:110px; height:40px; text-align:center; border-radius:5px; background-color:#183F97; font-size:15px; line-height:40px; color:#fff; font-weight:500; margin-bottom:10px; cursor:pointer;
                        img {display:inline-block; vertical-align:middle; margin-right:10px;}
                    }
                }
            }
        }
    }
}

















